<template>
  <div id="app" class="app" >
    <div class="left"  v-if="!isMobile">
      <menuNav></menuNav>
    </div>
    <div class="left"  v-else>
      <nav_mobile />
    </div>
    <div class="right">
        <div class="d-flex flex-column">
          <div class="header">
            <pageheader />
          </div>
          <div class="main">
            <router-view/>
          </div>       
        </div>
    </div>
  </div>
</template>
<script>
import menuNav from '../components/Layout/menuNav.vue';
import pageheader from '../components/Layout/PageHeader.vue';
import nav_mobile from '../components/Layout/nav_mobile.vue';
export default {
  data(){
    return{
      screenWidth: document.body.clientWidth,
      // isMobile:false
    }
  },
  //如果葉面刷新 重取username 存放store
  created(){
    let username =localStorage.getItem('userId');
    if(username){
       this.$store.dispatch('MemberDataModules/setUsername',username);
    }
    else{
      return false;
    }
  },
  computed:{
        isMobile(){
            return this.$store.state.CollapseModules.isMobile;
        },
  },
  mounted () {
    const that = this
    window.onresize = () => {
        return (() => {
            window.screenWidth = document.body.clientWidth
            that.screenWidth = window.screenWidth
        })()
    }
  },
  watch:{
    screenWidth(val){
        if(!this.timer){
            this.screenWidth = val
            this.timer = true
            let that = this
            setTimeout(function(){
                that.timer = false
            },400)
        }
        // if(this.screenWidth <= 1147){
        if(this.screenWidth >414){
          this.$store.dispatch('CollapseModules/mobileisFalse');
           this.$store.dispatch('CollapseModules/visibleisTrue');
          // this.$store.dispatch('CollapseModules/collapseisTrue')
          if(this.screenWidth <= 1000){
            this.$store.dispatch('CollapseModules/visibleisFalse')
            this.$store.dispatch('CollapseModules/collapseisTrue')
          }

          else{
            this.$store.dispatch('CollapseModules/collapseisFalse')
          }
        }
        else{
            this.$store.dispatch('CollapseModules/mobileisTrue')
            this.$store.dispatch('CollapseModules/visibleisFalse')
        }
        // else{
        //   this.$store.dispatch('CollapseModules/visibleisTrue')
        //   this.$store.dispatch('CollapseModules/collapseisFalse')
        // }
    }
  },
  components:{
    menuNav,
    pageheader,
    nav_mobile
  }
}
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
.app{
  display: flex;
}
.left{
  max-width: 14%;
  background-color: rgb(92, 128, 236);
}
.right{
  flex-grow:1;
}
.header{
   width: 100%; 
}
.main{
   width: 100%;
}
</style>
